.topNav {
  background: inherit;
  background-color: #616161;
  z-index: 2;
  opacity: 0.9;
  height: 50px;
  overflow: hidden;
  position: fixed;
  display: flex;
  top: 0;
  width: 100%;
  transition: all 1s ease;
  box-shadow: #616161 1px 1px 5px 1px;
}
.topNav span {
  font-size: 1em;
  font-weight: bold;
  -webkit-text-decoration: italic;
          text-decoration: italic;
  color: #F7F9FB;
}
.topNav .navHead {
  margin: auto 0 auto 10px;
  transition-delay: 2s;
  transition: all 0.1s;
  -webkit-animation: backup 1s ease;
          animation: backup 1s ease;
}
@-webkit-keyframes backup {
  0% {
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}
@keyframes backup {
  0% {
    -webkit-transform: translateX(40px);
            transform: translateX(40px);
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
  }
}
.topNav .navHead a {
  color: #F7F9FB;
  text-decoration: none;
}
.topNav .navHead a:link {
  color: #F7F9FB;
}
.topNav .navHead a:visited {
  color: #F7F9FB;
}
.topNav .navHead a:hover {
  color: #F7F9FB;
  font-size: 1.1em;
  opacity: 0.5;
}
.topNav .navHead a:active {
  color: #F7F9FB;
}

.menuButtonContainer {
  display: none;
  margin: auto 15px;
  margin-left: auto;
  opacity: 0.9;
  cursor: pointer;
  position: absolute;
  z-index: 2;
  right: 10px;
  top: 10px;
}
.menuButtonContainer div {
  width: 20px;
  height: 2px;
  background-color: #F7F9FB;
  margin: 4px 0;
  transition: 0.7s;
}
.menuButtonContainer .change.bar1 {
  -webkit-transform: translate(0, 6px) rotate(-45deg);
          transform: translate(0, 6px) rotate(-45deg);
}
.menuButtonContainer .change.bar2 {
  opacity: 0;
}
.menuButtonContainer .change.bar3 {
  -webkit-transform: translate(0, -6px) rotate(45deg);
          transform: translate(0, -6px) rotate(45deg);
}

.navList {
  grid-gap: 2em;
  gap: 2em;
  padding: 0;
  height: 30px;
  display: flex;
  margin: auto 0;
  justify-content: flex-start;
}
.navList .navItem {
  display: block;
  margin: auto 10px;
}
.navList .navLink {
  color: #F7F9FB;
  font-weight: light;
  text-decoration: none;
}

@media (max-width: 600px) {
  .navList .navItem:not(:first-child) {
    display: none;
  }
  .topNav.responsive {
    flex-flow: column;
    justify-content: flex-start;
    opacity: 1;
    height: 14em;
    border-bottom-left-radius: 4%;
  }
  .topNav.responsive .navList {
    border-top: 0.5px solid #F7F9FB;
    display: flex;
    flex-flow: column;
    align-items: left;
    grid-gap: 30px;
    gap: 30px;
    margin: 45px 0;
    -webkit-animation: widthGrowing 1s ease;
            animation: widthGrowing 1s ease;
  }
  @-webkit-keyframes widthGrowing {
    0% {
      width: 0%;
    }
    100% {
      width: 100%;
    }
  }
  @keyframes widthGrowing {
    0% {
      width: 0%;
    }
    100% {
      width: 100%;
    }
  }
  .topNav.responsive .navItem {
    display: block;
  }
  .topNav.responsive .navItem:nth-child(2) {
    margin-top: 20px;
  }
  .topNav.responsive .navLink {
    transition: all 0.5s ease;
  }
  .topNav.responsive .navLink:hover {
    opacity: 0.5;
    font-size: 1.1em;
    padding-left: 10px;
  }
  .topNav.responsive .navHead {
    position: absolute;
    top: 10px;
    left: 12px;
    margin: 0;
    padding: 0;
    -webkit-animation: dropdown 1s ease;
            animation: dropdown 1s ease;
  }
  @-webkit-keyframes dropdown {
    0% {
      -webkit-transform: translateX(40px);
              transform: translateX(40px);
      -webkit-transform: translateY(-200px);
              transform: translateY(-200px);
    }
  }
  @keyframes dropdown {
    0% {
      -webkit-transform: translateX(40px);
              transform: translateX(40px);
      -webkit-transform: translateY(-200px);
              transform: translateY(-200px);
    }
  }
  .menuButtonContainer {
    display: block;
  }
}

